<template>
  <div id="app">
    <img src="./assets/logo.png">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    mounted () {
      let vm = this
      window.addEventListener('batterystatus', vm.onBatteryStatus, false)
    },
    methods: {
      onBatteryStatus (status) {
        alert('Level: ' + status.level + ' isPlugged: ' + status.isPlugged)
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>



