import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component (resolve) {
        require(['../components/HelloWorld.vue'], resolve)
      }
    },
    {
      path: '/camera',
      name: 'camera',
      component (resolve) {
        require(['../components/camera.vue'], resolve)
      }
    },
    {
      path: '/battery',
      name: 'battery',
      component (resolve) {
        require(['../components/battery.vue'], resolve)
      }
    },
    {
      path: '/contacts',
      name: 'contacts',
      component (resolve) {
        require(['../components/contacts.vue'], resolve)
      }
    },
    {
      path: '/device',
      name: 'device',
      component (resolve) {
        require(['../components/device.vue'], resolve)
      }
    },
    {
      path: '/motion',
      name: 'motion',
      component (resolve) {
        require(['../components/device_motion.vue'], resolve)
      }
    },
    {
      path: '/orientation',
      name: 'orientation',
      component (resolve) {
        require(['../components/device_orientation.vue'], resolve)
      }
    },
    {
      path: '/dialogs',
      name: 'dialogs',
      component (resolve) {
        require(['../components/dialogs.vue'], resolve)
      }
    }
  ]
})



